import React, { createContext, useState, useContext } from 'react';


{/*dfini un fournisseur de contexte (UserProvider) qui utilise un tat pour grer les informations de l'utilisateur. Le hook personnalis useUser permet d'accder et de modifier l'tat de l'utilisateur dans les composants de l'application.*/ }


// Cration du contexte
const UserContext = createContext(null);

// Fournisseur de contexte
export const UserProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [isSignedIn, setIsSignedIn] = useState(false);
  const [userRole, setUserRole] = useState(null); // pour le rle
  const signOut = async () => {
    // Rinitialiser les tats
    setIsSignedIn(false);
    setUser(null);
    setUserRole(null);

    // Effacer le token de AsyncStorage
    await AsyncStorage.removeItem('userToken');
  };

  const contextValue = {
    isSignedIn,
    setIsSignedIn,
    user,
    setUser,
    userRole,    
    setUserRole,
    signOut,
  };




  return (
    <UserContext.Provider  value={contextValue}>
      {children}
    </UserContext.Provider>
  );
};

// Hook personnalis pour utiliser le contexte
export const useUser = () => {
  const context = useContext(UserContext);
  if (!context) {
    throw new Error('useUser doit tre utilis  l\'intrieur d\'un UserProvider');
  }
  return context;
};

export default UserContext;
